import React, { useState } from 'react';
import Typography from '@mui/material/Typography';
import { Button, Container, Stack } from '@mui/material';

const GoWasmComponent: React.FC = () => {
    const [ipResult, setIpResult] = useState("")

    function getCurrentIP() {
        fetch('https://api64.ipify.org?format=json')
            .then(response => {
                if (!response.ok) {
                    throw new Error('Network response was not ok');
                }
                return response.json();
            })
            .then(data => {
                console.log('Current IP:', data.ip);
                setIpResult(data.ip);
            })
            .catch(error => {
                console.error('Error:', error);
                setIpResult(error);
            });
    }

    return (


        <div>
            <Container fixed>
                <Stack spacing={3}>
                    <Stack spacing={2}>
                        <Button variant='contained' onClick={getCurrentIP}>Get Your IP</Button>
                        <Typography variant="h3" component="h3">Your IP: {ipResult}</Typography>
                    </Stack>
                </Stack>
            </Container>
        </div>
    );
}

export default GoWasmComponent;